.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}

.form-signin .checkbox {
  font-weight: 400;
}

.form-signin .form-floating:focus-within {
  z-index: 2;
}

.form-signin input[type='email'] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.form-signin input[type='password'] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

html {
  direction: rtl;
}

/* Popup style */
.popup-box {
  position: fixed;
  background: #00000050;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 999;
}

.box {
  text-align: center;
  position: relative;
  width: 45%;
  margin: 0 auto;
  height: auto;
  max-height: 85vh;
  margin-top: calc(100vh - 85vh - 20px);
  background: #fff;
  border-radius: 25px;
  padding: 20px;
  padding-bottom: 35px;
  border: 1px solid #999;
  overflow: auto;
}

.close-icon {
  content: 'x';
  cursor: pointer;
  position: fixed;
  right: calc(27% - 30px);
  top: calc(100vh - 85vh - 33px);
  background: #ededed;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  border: 1px solid #999;
  font-size: 20px;
}
.report_select {
  margin: auto;
  width: 400px;
  text-align: center;
  vertical-align: middle;
}

.transparent_button {
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
}

.navbar_links {
  color: rgb(189, 187, 187);
  text-decoration: none;
  font-size: large;
  font-family: Arial, Helvetica, sans-serif;
  padding: 0px 14px;
  /* border-left: 1px solid rgb(165, 165, 165); */
}
.navbar_links:hover {
  color: rgb(44, 167, 233);
}

.field_search {
  float: left;
  margin: 10px;
}

.debounce_search {
  margin-right: 10px;
}

.table_header {
  background-color: rgb(83, 83, 83);
  color: white;
  text-align: center;
}
.hoverStyles {
  position: absolute;
  background: #505050;
  color: white;
  padding-left: 1%;
  padding-right: 1%;
}
